.card {
  height: 403px;
  width: 200px;
  padding: 13px 14px;
  border: 1px solid $card-border;
  border-radius: 5px;

  font-size: 11px;

  &:hover {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
  }
  &__image {
    display: block;
    box-sizing: content-box;
    height: 134px;
    width: 160px;
    margin: 0 auto 28px;
    padding: 14px 0;
    border: 3px solid transparent;
    transition: border 0s, transform 1s;

    &:hover {
      box-sizing: content-box;
      border: 3px solid $card-border;
      transform: scale(1.2);
    }
  }

  &__name {
    margin-bottom: 5px;

    font-size: 12px;
    font-weight: 500;
    line-height: 15px;
  }

  &__code {
    line-height: 13px;
    margin-bottom: 15px;
  }

  &__rating {
    @include space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  &__price {
    @include space-between;
    align-items: flex-end;
    margin-bottom: 20px;

  }
  &__price-text {
    line-height: 13px;
    padding-bottom: 1px;
  }
  &__price-value {
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;

  }

  &__button {
    @include flex-center;
    width: 170px;
    height: 40px;
  
    background-color: $hover-color;
    border-radius: 5px;
    border-style: none;
  
    font-size: 14px;
    font-weight: bold;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;
    color: $button-text-color;

    transition: color 1s, background-color 1s, border 1s;
    &:hover {
      background-color: #fff;
      border: 1px solid $hover-color;
      color: $hover-color;
    }
  }
}
